import { useEffect } from 'react';

//Component imports
import Background from '../components/Background/Background';

//Library imports
import AOS from 'aos';

//Style imports
import 'aos/src/sass/aos.scss';
import './styles/index.scss';

export default () => {
  useEffect(() => {
    AOS.init({
      duration : 2000
    })
  },[])

  return (
    <div id='main-wrapper'>
      <Background />
    </div>
  )
}
  
